<template>
    <div>
        <el-button type="danger" @click="increment">增加</el-button>
         <el-button type="danger" @click="deletediv">减少</el-button>
        <div>现在数字为 {{count}}</div>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'

    export default {
        computed: mapGetters([
            'count'
        ]),
        methods: mapActions([
            'increment',
            'deletediv'
        ])
    }
</script>



