<template>
    <div id="user-manage">
        <div class="formtable">
            <el-form :inline="true" :model="UserData" class="demo-form-inline">
                <el-form-item label="想要修改的id">
                    <el-input v-model="UserData.id" placeholder="id"></el-input>
                </el-form-item>
                <el-form-item label="姓名">
                    <el-input v-model="UserData.name" placeholder="审批人"></el-input>
                </el-form-item>
                <el-form-item label="E-Mail">
                    <el-input v-model="UserData.email"></el-input>
                </el-form-item>
                <el-form-item label="密码">
                    <el-input v-model="UserData.password" type="password"></el-input>
                </el-form-item>
            </el-form>
        </div>
        <div class="formbutton">
            <el-button type="primary" @click="save">增加</el-button>
            <el-button type="primary" @click="update">修改</el-button>
            <el-button type="primary" @click="first">查询</el-button>
        </div>

        <div class="content" v-loading="loading">
            <el-table :data="userList" border size="small">
                <el-table-column type="index" label="序号" width="80"></el-table-column>
                <el-table-column prop="name" label="姓名" width="180"></el-table-column>
                <el-table-column prop="password" label="密码"></el-table-column>
                <el-table-column prop="email" label="邮箱"></el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                            size="mini"
                            type="primary" plain
                            @click="deletea(scope.$index,scope.row)">删除啊啊
                        </el-button>
                        <el-button
                            size="mini"
                            type="danger" plain
                            @click="del(scope.$index, scope.row)">删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script scoped>
    import Data from '@/views/Data'
    import axios from 'axios'

    export default {
        name: 'userManage',
        data() {
            return {
                UserData: {
                    name: '',
                    email: '',
                    password: '',
                    id: ''
                },
                userList: [],
                loading: false,
                deleteid: ''
            }
        },
        mounted() {
            this.first();
        },
        methods: {
            save() {
                this.$http.post('create_user', this.UserData)
            },
            update() {
                this.$http.post('update_user', this.UserData)
            },
            first() {
                this.loading = true
                this.$http.post('Query_user').then(res => {
                    this.loading = false
                    this.userList = res.data;
                })
            },
            deletea(index, row) {
                alert('biu')
                alert(row.id)
                    alert(index)
                this.$http.post('delete_user', this.deleteid).then(res => {
                    if (res.result) {
                        this.$message({
                            showClose: true,
                            message: '删除成功',
                            type: 'success'
                        });
                    }
                })
            }
        }

    }
</script>

<style scoped>

</style>



