import Vue from 'vue'
import Router from 'vue-router'
import Test from '@/views/test'
import Home from '@/views/home'
import News from '@/views/news'
import List from '@/views/list'
import VIEWUI from '@/views/ViewUI'
import TABLE from '@/views/teble'
import User from '@/views/user'
Vue.use(Router);
let router = new Router({
    routes: [
        {path: '/', redirect: '/test'},
        {title: 'test', path: '/test', name: 'test', component: Test},
        {title: 'home', path: '/home', name: 'home', component: Home},
        {title: 'news', path: '/news', name: 'news', component: News},
        {title: 'list', path: '/list', name: 'list', component: List},
        {title: 'ViewUI', path: '/ViewUI', name: 'VIEW', component: VIEWUI},
        {title: 'table', path: '/teble', name: 'table', component: TABLE},
        {title: 'user', path: '/user', name: 'user', component: User}
    ]
});

router.beforeEach((to, from, next) => {
    if (to.matched.length === 0) {
        from.name ? next({name: from.name}) : next('/');
    } else {
        next();
    }
});
export default router



