<template>
    <div id="user">
        <div class="userhader">
            <div style="float: left;margin-left: 540px;margin-top: 19px"><h1
                style="font-family: 华文行楷;color:#d1dbe5;font-size: 38px">用户管理</h1></div>
        </div>
        <router-link to="test">
            <el-button type="primary" icon="el-icon-back" style="">返回</el-button>
        </router-link>
        <div class="input">
            <el-form :inline="true" :model="housedata" class="demo-form-inline">
                <el-input v-model="housedata.size" placeholder="请输入房间大小" style="width: 180px;height: 20px"></el-input>
                <el-input v-model="housedata.position" placeholder="请输入房间位置"
                          style="width: 180px;height: 20px"></el-input>
                <el-input v-model="housedata.money" placeholder="请输入房子金额" style="width: 180px;height: 20px"></el-input>
                <el-input v-model="housedata.user_id" placeholder="请输入房间用户"
                          style="width: 180px;height: 20px"></el-input>
                <el-button type="primary" icon="el-icon-check" style="float: right;margin-top: 40px" @click="pick">配对
                </el-button>
            </el-form>
        </div>
        <div class="content" v-loading="loading">
            <el-table :data="userList.slice((currentPage-1)*pagesize,currentPage*pagesize)" border size="small">
                <el-table-column type="index" label="序号" width="80"></el-table-column>
                <el-table-column prop="id" label="编号" width="80"></el-table-column>
                <el-table-column prop="name" label="姓名" width="180"></el-table-column>
                <el-table-column prop="phone" label="手机"></el-table-column>
                <el-table-column prop="password" label="密码 " :formatter="formatSex"></el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                            size="mini"
                            type="primary" plain
                            @click="edit_operator(scope.$index, scope.row)">编辑
                        </el-button>
                        <el-button
                            size="mini"
                            type="danger" plain
                            @click="del(scope.$index, scope.row)">删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="page" style="margin-left: 600px">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[2, 3, 4, 5]"
                    :page-size="pagesize"
                    layout="total, sizes, prev, pager, next"
                    :total="userList.length">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                userList: [],
                currentPage: 1,
                pagesize: 2,
                housedata: {
                    size: '',
                    position: '',
                    money: '',
                    user_id: ''
                },
                loding: 'false'
            }
        },
        methods: {
            queryuser() {
                this.loding = true;
                this.$http.post('userquery').then(res => {
                    if (res.result) {
                        this.loding = false;
                        this.userList = res.data;
                    } else {
                        this.$message({
                            message: res.message,
                            type: 'error'
                        })
                    }
                })
            },
            handleSizeChange: function (size) {
                this.pagesize = size;
            },
            handleCurrentChange: function (currentPage) {
                this.currentPage = currentPage;
            },
            pick() {
                this.$http.post('addhouse', this.housedata).then(res => {
                    if (res.result) {
                        this.housedata = {}
                        this.$message({
                            message: '配对成功',
                            type: 'success'
                        })
                    } else {
                        this.$message({
                            message: '已有配对用户，请重新选择',
                            type: 'error'
                        })
                    }
                })
            },
            formatSex() {
                return '*****'
            }
        },
        mounted() {
            this.queryuser()
        }


    }
</script>

<style scoped>
    .userhader {
        width: 1800px;
        height: 100px;

        background-color: deepskyblue;
    }

    .input {
        width: 800px;
        height: 120px;
        margin-left: 300px;
        padding-left: 30px;
    }

    .content {
        width: 1200px;
        height: 800px;
        margin-top: 20px;
        margin-left: 50px;
    }
</style>



