<template>
    <div id="box">
        <div class="hader">
            <div style="float: left;margin-left: 520px;margin-top: 19px"><h1
                style="font-family: 华文行楷;color:#d1dbe5;font-size: 38px">房屋管理平台</h1></div>
            <el-input v-model="input" placeholder="请输入内容"
                      style="width: 200px;float: right;margin-right: 600px;margin-top: 60px">
            </el-input>
            <el-button icon="el-icon-search" circle
                       style="float: right;margin-right: -240px;margin-top: 60px" @click="select"></el-button>
        </div>
        <div class="bottom" v-loading="loding">
            <el-button type="primary" icon="el-icon-circle-plus" style="margin-left: 1100px;float: right" @click="add">
                添加
            </el-button>
            <router-link to="user">
                <el-button type="primary" icon="el-icon-circle-plus"
                           style="margin-left: 980px;float: left;margin-top: -40px">
                    用户管理
                </el-button>
            </router-link>
            <el-table
                :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                stripe
                style="width: 100%">
                <el-table-column
                    type="index"
                    label="序列"
                    width="100">
                </el-table-column>
                <el-table-column
                    prop="houseid"
                    label="房屋编号"
                    width="100">
                </el-table-column>
                <el-table-column
                    prop="money"
                    label="房屋价格"
                    width="100">
                </el-table-column>
                <el-table-column
                    prop="position"
                    label="房屋位置"
                    width="180">
                </el-table-column>
                <el-table-column
                    prop="size"
                    label="房屋大小"
                    width="120">
                </el-table-column>
                <el-table-column
                    prop="user_name"
                    label="姓名"
                    width="100">
                </el-table-column>
                <el-table-column
                    prop="phone"
                    label="手机号"
                    width="160">
                </el-table-column>
                <el-table-column
                    prop="state"
                    label="状态">
                </el-table-column>
                <el-table-column
                    :formatter="formatSex"
                    prop="pwd"
                    label="密码">
                </el-table-column>
                <el-table-column label="操作" width="160">
                    <template slot-scope="scope">
                        <el-button
                            size="mini"
                            type="primary" plain
                            @click="edit_operator(scope.$index, scope.row)">编辑
                        </el-button>
                        <el-button
                            size="mini"
                            type="danger" plain
                            @click="del(scope.$index, scope.row)">删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="page" style="margin-left: 600px">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[2, 3, 4, 5]"
                    :page-size="pagesize"
                    layout="total, sizes, prev, pager, next"
                    :total="tableData.length">
                </el-pagination>
            </div>
        </div>
        <ViewUI :showDialogData="showUserDialog" @save="save"></ViewUI>
    </div>

</template>
<script>
    import ViewUI from '@/views/ViewUI'

    export default {
        components: {
            ViewUI
        },
        data() {
            return {
                input: '',
                currentPage: 1,
                pagesize: 2,
                tableData: [],
                showUserDialog: {
                    is_show: false,
                    operator: '',
                    title: '',
                    data: {}
                },
                loading: 'false'
            }
        },
        methods: {
            add() {
                this.showUserDialog.is_show = true;
                this.showUserDialog.operator = 'add';
                this.showUserDialog.title = '添加用户';
                this.showUserDialog.data = {}
            },
            edit_operator(index, row) {
                this.showUserDialog.is_show = true;
                this.showUserDialog.operator = 'edit';
                this.showUserDialog.title = '编辑用户';
                this.showUserDialog.data = {
                    'name': row.user_name,
                    'phone': row.phone,
                    'password': row.pwd
                }
            },
            save(item) {
                if (item.operator === 'add') {
                    this.tableData.push(item.data)
                }
                if (item.operator === 'edit') {
                    this.select();
                    this.tableData.splice(item.index, 1, item.data)
                }
            },
            select() {
                this.loading = true
                this.$http.post('query_user', {cond: this.input}).then(res => {
                    this.loading = false;
                    if (res.result) {
                        this.tableData = res.data;
                    }
                })
            },
            formatSex() {
                return '**********'
            },
            del(index, row) {
                this.$http.post('delete_user?id=' + row.houseid, {}).then(res => {
                    if (res.result) {
                        this.tableData.splice(index, 1);
                        this.$message({
                            showClose: true,
                            message: '删除成功',
                            type: 'success'
                        });
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.message,
                            type: 'error'
                        });
                    }
                })
            },
            // 初始页currentPage、初始每页数据数pagesize和数据data
            handleSizeChange: function (size) {
                this.pagesize = size;
            },
            handleCurrentChange: function (currentPage) {
                this.currentPage = currentPage;
            },
        },
        mounted() {
            this.select()
        }
    }


</script>

<style scoped>
    .hader {
        width: 1800px;
        height: 100px;
        background-color: deepskyblue;
    }

    .bottom {
        width: 1200px;
        height: 800px;
        margin-top: 100px;
        margin-left: 50px;
    }

</style>



