<template>
    <div>
        <p>{{($route.params.color)}}</p>
     <h1>{{msg}}</h1>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                msg: '我是主页'
            }
        },
        methods: {
            change() {
                var apple = this.msg
                this.msg = apple
            }
        }
    }
</script>

<style scoped>

</style>



